//用grp_id取得群組成員
export const getGroupMember = async (userId, grpid) => {
  if(AJADev) {
    return mock.getGroupMember
  }
  return await axiosWrap('Post', `/RestApiAdapter/MXG0_0000/G11`, reqBodyMaker.queryGroup(userId, grpid, null, null, null, 'G11'), null, '群組成員byGrpId').then(result => {
    if(result.body.groups) {
      let checkedDatas =  result.body.groups.filter(item => item.ID).map(item => {
        return {
          grp_id: item.GRP_ID,
          id: item.ID,
          name: item.NAME || `noNameData-${item.ID}`,
          birthday: item.BIRTHDAY || '',
          mobile: item.MOBILE || '',
          home_tel: item.HOME_TEL || '',
          home_tel_area: item.HOME_TEL_AREA || '',
          vip_lvl: item.vip_lvl || '',
          sex: item.SEX || '',
          age: item.AGE 
        }
      })
      console.log('for 假資料');
      
      console.log(uniqBy(checkedDatas, (item)=> {return item.id}));
      
      return uniqBy(checkedDatas, (item)=> {return item.id})
    } else {
      return []
    }
  })
}

